class GameUI {
    constructor(options) {
        this.ctx = options.ctx;
        this.game = options.game;
        this.gridHeight = options.width || 480;
        this.gridWidth = options.height || 640;
    }

    drawGrid () {
        this.ctx.width = this.gridWidth;
        this.ctx.height = this.gridHeight;
    }
}

export default GameUI;



