import GameUI from './ui.js';
import GameCore from './game.js';

let canvas = document.getElementById('game-grid');

let core = new GameCore();
let ui = new GameUI(
    {
        ctx: canvas.getContext('2d'),
        game: core,
        width: 480,
        height: 640
    }
);

ui.drawGrid();



